                isShow:false,
            }
        },
        created(){
            this.axios.get("/v1/user").then(res=>{
                console.log(res)
                this.user = res;
            }).then(err=>{})
        },
        methods:{
            Cancellation(){
                localStorage.removeItem("user_id");
                localStorage.removeItem("user");
                this.axios.get("/v2/signout").then(res=>{
                    console.log(res);
                    localStorage.removeItem("shops");
                }).catch(err=>{});
                this.$router.go(-1);
            },
        }
    }
